.portfolio-container {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    color: #333;
  }
  
  header {
    background-color: #4c6ef5;
    color: white;
    padding: 20px;
    text-align: center;
    border-radius: 8px;
  }
  
  nav button {
    margin: 0 10px;
    padding: 8px 16px;
    background-color: #3b5bdb;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  nav button.active, nav button:hover {
    background-color: #364fc7;
  }
  
  .home-section, .portfolio-section {
    margin-top: 20px;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  }
  
  .portfolio-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .portfolio-card {
    background-color: #f8f9fa;
    border-radius: 8px;
    overflow: hidden;
    transition: box-shadow 0.3s;
  }
  
  .portfolio-card:hover {
    box-shadow: 0 6px 15px rgba(0,0,0,0.2);
  }
  
  .portfolio-card img {
    width: 100%;
    height: 180px;
    object-fit: cover;
  }
  
  .portfolio-card h3 {
    padding: 10px;
    text-align: center;
  }
  
  footer {
    margin-top: 30px;
    padding: 10px;
    text-align: center;
    color: #888;
    border-top: 1px solid #ddd;
  }
  